/* animacion  */
.animation-initial {
  opacity: 0;
  transition: all 0.5s;
}
.animation-initial-showUp {
  opacity: 0;
  transition: all 0.5s;
}
.animation-initial-showDown {
  opacity: 0;
  transition: all 0.5s;
}
.animation-initial-showLeft {
  opacity: 0;
  transition: all 0.5s;
}
.animation-initial-showRight {
  opacity: 0;
  transition: all 0.5s;
}

/* hacia arriba */
.showUp {
  animation: showUp 1s;
}

/* hacia abajo  */
.showDown {
  animation: showDown 1s;
}

/* hacia la izquierda */
.showLeft {
  animation: showLeft 1s;
}

/* hacia la derecha  */
.showRight {
  animation: showRight 1s;
}

@keyframes showUp {
  0% {
    transform: translateY(5.75rem);
  }
  100% {
    transform: translateY(0);
  }
}
@keyframes showDown {
  0% {
    transform: translateY(-5.75rem);
  }
  100% {
    transform: translateY(0);
  }
}
@keyframes showLeft {
  0% {
    transform: translateX(5.75rem);
  }
  100% {
    transform: translateX(0);
  }
}
@keyframes showRight {
  0% {
    transform: translateX(-5.75rem);
  }
  100% {
    transform: translateX(0);
  }
}